<template>
	<div class="assets">

		<div style="background: white;">
			<mu-list style="margin: 0;padding: 0;">
				<mu-list-item v-for="item,index in assetsList" :key="index" avatar button :ripple="false" style="padding: 8px 0;">
					<mu-list-item-action>
						<mu-avatar>
							<img :src="getLogo(item.name)" width="30" height="30" style="border-radius: 50%;" />
						</mu-avatar>
					</mu-list-item-action>
					<mu-list-item-title>{{item.name}}</mu-list-item-title>
					<mu-list-item-action>
						<mu-button icon @click="changeChoose(item.name)">
							<i class="iconfont" :style="{color:item.isChoose?'#B9862F':'#DCDDDD'}">&#xe627;</i>
						</mu-button>
					</mu-list-item-action>
				</mu-list-item>
			</mu-list>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Search',
		data() {
			return {
				keyword: '',
				selects: [],
				assetsList: [{
					name:'ALD',
					isChoose:true,
					id:1
				},{
					name:'ETH',
					isChoose:true,
					id:2
				},{
					name:'BTC',
					isChoose:true,
					id:3
				},{
					name:'EOS',
					isChoose:true,
					id:4
				},{
					name:'USDT',
					id:5
				},{
					name:'AE',
					id:6
				},{
					name:'BAT',
					id:7
				},{
					name:'BNB',
					id:8
				},{
					name:'HT',
					id:9
				},{
					name:'KNC',
					id:10
				},{
					name:'OMG',
					id:11
				},{
					name:'PAY',
					id:12
				},{
					name:'ZRX',
					id:13
				}]
			}
		},
		computed: {
			getLogo() {
				return function(value) { 
//					return require('../../assets/img/' + value + '.png')
				}
			},
		},
		methods: {
			routerBack() {
				this.$router.back();
			},
			clear() {
				// 清除
			},
			changeChoose(name) {
				// 是否选择
				let arr = this.assetsList;
				let chooseAssetsArray=[]
				for(let i in arr) {
					if(name == arr[i].name) {
						this.$set(this.assetsList[i], 'isChoose', arr[i].isChoose?false:true);
					}
					if(arr[i].isChoose) {
						chooseAssetsArray.push(this.assetsList[i]);
					}
				}
				let chooseCoinsTypeArr = []
				for(let i in chooseAssetsArray){
					chooseCoinsTypeArr.push(chooseAssetsArray[i].name)
				}
				//暂存，返回首页直接更新
				this.$store.commit('backCoinsList',chooseCoinsTypeArr)
				//选中的资产存本地
				localStorage.setItem('chooseCoinsTypeArr',JSON.stringify(chooseCoinsTypeArr))
			},
		},
		mounted() {
			let localStorageAssets = JSON.parse(localStorage.getItem('chooseCoinsTypeArr'));
//			for(let i in this.$lib.coinsList){
//				newCoinsList.push({
//					name:this.$lib.coinsList[i],
//					type:i
//				})
//			}
			console.log(this.$lib.libAssetsList)
			let newCoinsList = this.assetsList;
			console.log(newCoinsList)
			if(localStorageAssets){
				for(let i in newCoinsList) {
					for(let j in localStorageAssets){
					  //判断选中状态,
						if( newCoinsList[i].name == localStorageAssets[j]){
							this.$set(newCoinsList[i], 'isChoose', true);
						}
					}
				}
			}else if(this.$lib.libAssetsList.length>0){
				//如果有本地存储，显示以前添加的资产
				for(let i in newCoinsList) {
					for(let j in this.$lib.libAssetsList){
						//判断选中状态,
						if(newCoinsList[i].name == this.$lib.libAssetsList[j].name){
							this.$set(newCoinsList[i], 'isChoose', true);
						}
					}
				}
				console.log("newCoinsList",newCoinsList)
			}
			console.log(newCoinsList)
			this.assetsList = newCoinsList;
		},
//		mounted() {
//			
//		},
		watch: {
		
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.assets {
		
	}
</style>


